.keunggulan {
    padding: 2rem 0;
    background: white;
    
    h1,h2,h3 {
        text-align: center;
        font-weight: 400;
        margin: 0;
    }
    .headline {
        &::after {
            content: '';
            display: block;
            height: 2px;
            background: black;
            opacity: 0.3;
            left: 0;
            right: 0;
            margin: 1rem auto;
            @include breakpoint-up(large){
                max-width: 74rem !important;
            }
            @include breakpoint-up(medium){
                max-width: 43.125rem;
            }
        }
    }
    .display-layanan {
        display: grid;
        gap: 2rem;
        align-content: space-between;
        align-items: center;
        justify-content: center;
        padding: 1rem;
        color: black;
        
        @include breakpoint-up(large) {
            grid-template-columns: repeat(3, auto) !important;
        }

        @include breakpoint-up (medium) {
            grid-template-columns: repeat(2, auto);
        }

        @include breakpoint-down(small) {
            grid-template-columns: repeat(1, auto);
        }

        .keunggulan-home {
            display: grid;
            width: 220px;
            height: 190px;
            grid-template-rows: repeat(2, 3rem);
            grid-gap: 1rem;
            align-content: space-around;
            align-items: center;
            justify-content: center;
            background: $secondary-color;
            text-align: center;
            padding: 1.725rem;
            border-radius: 20px;

            i {
                color: $secondary-lebih-gelap;
                font-size: 3rem;
            }

            
        }

        &-last {
            display: flex;
            gap: 2rem;
            flex-wrap: wrap;
            align-content: space-between;
            align-items: center;
            justify-content: center;
            padding: 1rem;
            color: black;
            
            .keunggulan-home {
                display: grid;
                width: 220px;
                height: 190px;
                grid-template-rows: repeat(2, 3rem);
                grid-gap: 1rem;
                align-content: space-around;
                align-items: center;
                justify-content: center;
                background: $secondary-color;
                text-align: center;
                padding: 1.725rem;
                border-radius: 20px;
    
                i {
                    color: $secondary-lebih-gelap;
                    font-size: 3rem;
                }
            }
        }
    }
}