.layanan {
    padding: 2rem 0;
    background: lightgrey;
    
    h1,h2,h3 {
        text-align: center;
        font-weight: 400;
        margin: 0;
    }
    
    .display-layanan {
        display: grid;
        align-items: center;
        justify-content: center;
        grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
        grid-gap: 3rem;
        padding: 0 4rem;

        .layanan-home {
            display: grid;
            grid-template-rows: repeat(4, 3rem);
            grid-gap: 0.2rem;
            align-items: center;
            justify-content: center;
            background: white;
            text-align: center;
            padding: 1.5rem;

            i {
                color: $primary-color;
                font-size: 3rem;
            }

            a {
                margin-top: 0.3rem;
                padding: 0.25rem;
                color: $primary-color;
                border: 2px solid $primary-color;
                transition: all 150ms ease-in-out;

                &:hover {
                    background: $primary-color;
                    color: $secondary-color;
                }
            }
        }
    }

}