.header {
    position: sticky;
    top: 0px;
    background: white;
    font-family: 'Merriweather', serif;
    z-index: 999;

    &.open {
        .header--button{
            >span:first-child{
                transform: rotate(45deg) translate(3.75px, 7px);
            }

            >span:nth-child(2){
               opacity: 0;
            }

            >span:last-child{
            transform: rotate(-45deg) translate(2px, -7px);
            }
        }
    }

    nav {
        position: relative;
        background: white;
        padding-top: 0.75rem;
        padding-bottom: 0.75rem;
    }

    &--logo{
        img{
            width: 132px;
            height: auto;
        }
    }

    &--button{
        >span {
            display: block;
            width: 28px;
            height: 2px;
            background-color: $primary-color;
            transition: all 300ms ease-in-out;
            transform-origin: center;

            &:not(:last-child){
                margin-bottom: 5px;
            }
        }
    }

    &--menu {
        position: absolute;
        width: calc(100%-3rem);
        left: 50%;
        transform: translateX(-50%);
        background: white;
        padding: 1.625rem;
        margin-top: 1.5rem;
        border-radius: 5px;

        a {
            display: block;
            color: $secondary-lebih-gelap;
            padding: 0.625rem;
            text-align: center;
        }
    }

    &--links {
        a {
            position: relative;
            font-size: 1rem;
            color: $secondary-lebih-gelap;
            transition: color 150ms ease-in-out;
            font-weight: 400;

            &:not(:last-child){
                margin-right: 32px;
            }

            &::before {
                content: "";
                position: absolute;
                display: block;
                height: 5px;
                background: $primary-color;
                left: 0;
                right: 0;
                bottom: -25px;
                background: $primary-color;
                opacity: 0;
                transition: opacity 150ms ease-in-out;
            }

            &:hover{
                color:$biru-ungu-muda;

                &::before{
                    opacity: 1;
                }
            }
        }
    }

    &--wa{
        font-size: $font-sm;
        font-weight: 700;
        transition: all 150ms ease-in-out;
        
        &:hover{
            background: $secondary-color;
            color: black;
        }
    }
}

.overlay {
    visibility: hidden;
    position: fixed;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background: linear-gradient($primary-color, transparent);
}