.detaillayanan {
    padding-top: 2rem;
    padding-bottom: 3rem;

    h1,h2,h3 {
        padding: 0;
        margin: 0;
        font-weight: 400;
        text-align: center;
    }

    h4 {
        padding: 0;
        margin: 0;
        font-weight: 700;
    }

    .konten-layanan {
        display: grid;
        @include breakpoint-up(large) {
            grid-template-columns: 1fr 2fr;
        }

        @include breakpoint-down (medium) {
            grid-template-columns: 1fr;
            justify-items: center;
        }
        justify-content: center;
        align-items: center;

        .gambar-layanan {
            width: 20rem;
            height: 13.333125rem;
            border-radius: 0.5rem;
        }
    }

    .gg {
        grid-row-gap: 3rem;
    }
}

.satu {
    background-color: lightgray;
}
.dua {
    background-color: white;
}

